import React, { useRef, useState } from "react";

const AddTask = ({ tasks, setTasks }) => {
  const [task, setTask] = useState("");

  const inputRef = useRef(null);

  //add task handler event
  const addTaskHandler = (e) => {
    e.preventDefault();
    inputRef.current.blur();
    //add task handle event
    taskPosting(task);
    setTask("");
  };

  //task posting
  //use text
  const taskPosting = async (text) => {
    const res = await fetch("https://keen-quick-airport.glitch.me/tasks", {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({ text }),
    });
    //real time data addition
    const data = await res.json();
    setTasks([...tasks, data]);
  };
  return (
    <form
      className="bg-gray-900 container mx-auto flex justify-between p-10"
      onSubmit={addTaskHandler}
    >
      <input
        ref={inputRef}
        value={task}
        onChange={(e) => setTask(e.target.value)}
        type="text"
        required
        placeholder="What things to do?"
        className="bg-transparent outline-none border-b-2 py-2 px-5 focus:border-teal-600"
      />
      <button
        type="submit"
        className="bg-teal-900/30 py-2 px-5 border-2 border-teal-500 rounded text-teal-500 hover:bg-teal-500 hover:text-gray-900 duration-300 "
      >
        Add task
      </button>
    </form>
  );
};

export default AddTask;
